.pageTitle {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 20px;
  }
  
  .customModal .modal-content {
    border-radius: 20px;
    padding: 20px;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
    border: none;
  }
  
  .customModal .modal-header {
    background-color: #f8f9fa;
    border-bottom: none;
    padding-bottom: 0;
  }
  
  .customModal .modal-title {
    font-size: 20px;
    font-weight: 600;
  }
  
  .customModal .modal-body input,
  .customModal .modal-body select {
    border-radius: 8px;
  }
  
  .customModal .modal-footer {
    border-top: none;
    padding-top: 0;
  }
  